import Vue from "vue";
import VueMask from "v-mask";
import App from "./App.vue";
import * as axios from "axios";

window.XClaimAuth = config => {
  return {
    show: () => {
      Vue.prototype.$http = axios.create({
        baseURL: config.baseURL
      });
      Vue.config.productionTip = false;
      Vue.use(VueMask);
      new Vue({
        props: { config: Object },
        render: h => {
          return h(App, { props: { config } });
        }
      }).$mount("#auth-app");
      setTimeout(() => {
        document.querySelector("#phoneNumber").focus();
      }, 100);
    },
    isLoggedIn: () => {
      return axios.head(config.baseURL, { withCredentials: true });
    },
    logout: () => {
      return axios.delete(config.baseURL, { withCredentials: true });
    },
    getUserInfo: () => {
      return axios.get(config.baseURL, { withCredentials: true });
    }
  };
};

window.addEventListener("load", () => {
  const { XClaimAuth } = window;
  const auth = new XClaimAuth({
    baseURL: "https://offers-dev.xclaimmb.com/v1/auth",
    authURL: "authURL",
    iconURL:
      "https://xclaimapihub.blob.core.windows.net/images-blob/Logo-color.png",
    applicationName: "XClaim Admin Portal",
    titleMessage: "titleMessage",
    primaryColor: "#3c8dbc",
    redirectUrl: "https://admin-dev.xclaimmb.com"
  });
  auth.show();
  auth
    .isLoggedIn()
    .then(() => {
      console.log("success");
    })
    .catch(() => {
      console.log("error");
    });
});
