<script>
export default {
  name: "LogIn",
  props: {
    config: Object
  },
  data: () => {
    return {
      phoneNumberMask: "(###) ###-####",
      verificationCodeMask: "######",
      errorMessage: "",
      phoneNumber: "",
      verificationCode: "",
      verificationCode0: "",
      verificationCode1: "",
      verificationCode2: "",
      verificationCode3: "",
      verificationCode4: "",
      verificationCode5: "",
      submitted: false,
      submitting: false,
      verifying: false
    };
  },
  methods: {
    submit() {
      this.submitting = true;

      const phoneNumber = this.phoneNumber.match(/[0-9]+/g).join("");
      this.$http(phoneNumber, {
        method: "post",
        data: {},
        withCredentials: true
      })
        .then(() => {
          // data, status, request
          this.submitted = true;
          this.submitting = false;
          setTimeout(() => {
            document.getElementById("verificationCode0").focus();
          });
        })
        .catch(error => {
          const statusCode = error.response.status;
          if (statusCode === 404) {
            this.errorMessage = "Phone number not associated with a user.";
          } else {
            this.errorMessage = "Invalid phone number.";
          }

          this.submitting = false;
        });
    },
    verify() {
      this.verifying = true;
      const phoneNumber = this.phoneNumber.match(/[0-9]+/g).join("");
      this.$http("", {
        method: "post",
        data: {
          oneTimePassword: this.verificationCode,
          phoneNumber
        },
        withCredentials: true
      })
        .then(() => {
          window.location.href = this.config.redirectUrl;
        })
        .catch(() => {
          this.errorMessage = "Invalid verification code.";
          this.verifying = false;
        });
    },
    focus(elemId, event) {
      const key = parseInt(event.key);
      if (event.which === 8 && this.submitted) {
        let currentInputNumber = parseInt(elemId.substr(elemId.length - 1));
        if (isNaN(currentInputNumber)) {
          currentInputNumber = 6;
        }
        this[`verificationCode${currentInputNumber - 1}`] = "";
        document
          .getElementById(`verificationCode${currentInputNumber - 2}`)
          .focus();
        return;
      }
      this.errorMessage = "";
      if ((key >= 0 && key <= 9) || elemId === "next") {
        setTimeout(() => {
          document.getElementById(elemId).focus();
        }, 50);
      }
      this.verificationCode =
        this.verificationCode0 +
        this.verificationCode1 +
        this.verificationCode2 +
        this.verificationCode3 +
        this.verificationCode4 +
        this.verificationCode5;
    }
  }
};
</script>

<template>
  <div class="login">
    <div class="modal is-active">
      <div class="modal-background"></div>

      <div class="modal-card">
        <header class="modal-card-head">
          <img v-bind:src="config.iconURL" class="application-icon">
          <p class="modal-card-title">{{config.applicationName}}</p>
        </header>
        <section class="modal-card-body">
          <label
            v-if="!submitted"
            class="label has-text-centered"
            for="phoneNumber"
          >Please enter your phone number</label>
          <input
            v-if="!submitted"
            class="input phoneNumber"
            id="phoneNumber"
            v-on:keyup="focus('next', $event);"
            type="text"
            v-mask="phoneNumberMask"
            v-model="phoneNumber"
            placeholder="(___) ___-____"
          >
          
          <label
            v-if="submitted"
            class="label has-text-centered"
            for="phoneNumber"
          >Please enter the 6-digit confirmation code</label>
          <div v-if="submitted" class="is-flex verification-code">
            <input
              class="input verification-code-input"
              id="verificationCode0"
              v-on:keyup="verificationCode0 && focus('verificationCode1', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode0"
              placeholder="#"
            >
            <input
              class="input verification-code-input"
              id="verificationCode1"
              v-on:keyup="focus('verificationCode2', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode1"
              placeholder="#"
            >
            <input
              class="input verification-code-input"
              id="verificationCode2"
              v-on:keyup="focus('verificationCode3', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode2"
              placeholder="#"
            >
            <input
              class="input verification-code-input"
              id="verificationCode3"
              v-on:keyup="focus('verificationCode4', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode3"
              placeholder="#"
            >
            <input
              class="input verification-code-input"
              id="verificationCode4"
              v-on:keyup="focus('verificationCode5', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode4"
              placeholder="#"
            >
            <input
              class="input verification-code-input"
              id="verificationCode5"
              v-on:keyup="focus('verify', $event)"
              type="text"
              v-mask="'#'"
              v-model="verificationCode5"
              placeholder="#"
            >
          </div>
          <div class="resend-code" v-if="submitted">
            <a v-on:click="submit()">Resend Code</a>
          </div>
          <article v-if="errorMessage" class="message is-danger error-message">
            <div class="message-body">{{errorMessage}}</div>
          </article>
        </section>
        <footer class="modal-card-foot">
          <button
            id="next"
            class="button is-primary is-fullwidth"
            v-bind:class="{ 'is-loading': submitting }"
            v-bind:style="{ backgroundColor: config.primaryColor, borderColor: config.primaryColor, boxShadow: 'none'}"
            v-on:click="submit()"
            v-if="!submitted"
            :disabled="phoneNumber.length !== 14"
          >Next</button>
          
          <button
            id="verify"
            class="button is-primary is-fullwidth"
            v-bind:class="{ 'is-loading': verifying }"
            v-bind:style="{ backgroundColor: config.primaryColor, borderColor: config.primaryColor, boxShadow: 'none'}"
            v-if="submitted"
            :disabled="verificationCode.length !== 6"
            v-on:click="verify()"
          >Verify</button>
        </footer>
      </div>
    </div>
  </div>
</template>

